/* ----------- */
/* VIDEOABOVE */

.header-section+div>section.info-hero {
    padding-top: 72px;
}

.info-hero {
    min-height: 300px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 40px 0;
    background-color: #6c7378;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: #fff;
}

.info-hero .container {
    width: 100%;
}


.custom-infohero .info-hero-container {
    /* putting the button $ h1 in front */
    z-index: 1;
}

h1.info-hero-head {
    font-size: 1.875rem;
    line-height: 2.25rem;
    font-family: "PayPalSansBig-Light", "Open Sans", sans-serif, "Helvetica Neue", Arial, sans-serif;
    font-weight: 300;
}


.btn-hero:hover,
.btn-hero:link,
.btn-hero:active,
.btn-hero:visited {
    background: #fcbb32;
    color: #0d3685;
    text-decoration: none;
}

.btn-hero {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    float: none;
    position: relative;
    max-width: 320px;
    padding: 15px 24px;
    background: #0070ba;
    font-size: 15px;
    font-size: .9375rem;
    font-family: "PayPalSansSmall-Light", "Open Sans", sans-serif, "Helvetica Neue", Arial, sans-serif;
    font-weight: 700;
    line-height: 22px;
    color: #fff;
    text-align: center;
    text-decoration: none;
    border-radius: 25px;
    border: none;
    box-sizing: border-box;
}

.info-hero-container .info-hero-container h1 {
    font-size: 2rem;
    line-height: 3rem;
    margin: .67em 0;
    font-family: "PayPalSansSmall-Light", "Open Sans", sans-serif, "Helvetica Neue", Arial, sans-serif;
    font-weight: 400;
}


/* ---------- */
/* VIDEO */
.bg-video-container {
    background-color: #0097e0;
    margin-bottom: -7px;
    position: relative;
    height: 300px;
}
.bg-video-container .bg-video {
    object-fit: cover;
    position: absolute;
    bottom: 0;
}
video {
    width: 100%;
}


/* --------- */
/* BUSINESS */
.notification-ribbon {
    background-color: #0d3685;
    padding: 30px 0;
    text-align: center;
}

.notification-ribbon .container{
padding-left: 24px;
padding-right: 24px;
}

.notification-ribbon h4 {
    font-size: 1.8rem;
    line-height: 1.875rem;
    vertical-align: middle;
    display: inline-block;
    font-weight: 400;
}
.btn-round {
    margin: 18px 0;
    position: relative;
    display: flex;
    background: #0070ba;
    background-color: transparent;
    color: #0070ba;
    border: .5px solid #0070ba;

}
.btn.btn-round {
    margin: 10px;
    display: inline-block;
    padding-top: 11px;
    padding-bottom: 11px;
}
.notification-ribbon .btn-round {
    display: inline-block;
    background-color: #fff;
    border-color: #fff;
    color: #0d3685;
    border-radius: 25px;
    font-weight: 700;
    vertical-align: middle;
    padding: 12px 18px;
    font-size: 12px;
    font-size: .75rem;
    line-height: 12px;
}
.notification-ribbon a:hover.btn-round {
    text-decoration: none;
}


